@use "../variables";

// Container mixins

@mixin make-container($gutter: variables.$container-padding-x) {
  --#{variables.$prefix}gutter-x: #{$gutter};
  --#{variables.$prefix}gutter-y: 0;
  width: 100%;
  padding-right: calc(
    var(--#{variables.$prefix}gutter-x) * 0.5
  ); // stylelint-disable-line function-disallowed-list
  padding-left: calc(
    var(--#{variables.$prefix}gutter-x) * 0.5
  ); // stylelint-disable-line function-disallowed-list
  margin-right: auto;
  margin-left: auto;
}
